@import "~react-image-gallery/styles/css/image-gallery.css";

/* Some initial config */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: #262626;
    font-family: "Open Sans", -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    user-select: none;
}

html {
    scroll-behavior: smooth;
}

img {
    pointer-events: none;
}

h1, h2, h3, h4, h5, h6 {
    font-family: "Droid Sans", -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

p {
    font-size: 1.2em;
}

body, .root-container {
    width: 100%;
}

body {
    background: #ededed;
}

.main-container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
}

.header-image {
    width: 100%;
    background: url("./assets/header-background.svg") no-repeat;
    background-size: cover;
    height: 100vh;
    background-attachment: fixed;
}

.header-container {
    width: 100%;
}

.icon-container {
    margin: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: fit-content;
    height: fit-content;
    display: none;
}

.icon-container i {
    border: 2px solid #fff;
    width: 100%;
    transition: all .3s;
}
.icon-container i:hover {
    transition: all .3s;
    background:rgba(0,0,0,0.2);
}

.menu-container {
    width: 100%;
}

.menu-container ul {
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

.menu-container ul li {
    list-style-type: none;
    margin: 16px;
}

.menu-container ul li a {
    display: block;
    padding: 0.5em 1em;
    color: #fff;
    border: 2px solid #fff;
    text-decoration: none;
    font-weight: normal;
    background:transparent;
    transition: all .3s;
    font-weight: 600;
}

.menu-container ul li a i {
    color: #fff;
    margin-right: 8px;
}

.menu-container ul li a:hover {
    transition: all .3s;
    background:rgba(0,0,0,0.2);
}

.main-container .presentation-container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.main-container .presentation-container .info-container {
    position: absolute;
    top: 30%;
    translate: (0, 30%);
    text-align: center;
    width: 100%;
    text-align: center;
}

.info-container .name-container {
    font-size: 3em;
    margin-bottom: 16px;
    text-align: center;
}

.info-container .name-container h1,
.info-container .phrase-container p {
    font-family: "Droid Sans";
    text-shadow: 2px 0px 7px rgba(38,38,38,0.62);
    color: #fff;
}

.info-container .phrase-container p {
    font-size: 2em;
}

.arrow-scroll {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 100px auto 0;
    width: 40px;
    background: transparent;
}

.arrow-scroll a {
    border: 2px solid #fff;
    display: block;
    padding: 16px 20px;
    border-radius: 100%;
    transition: all .3s;
}

.arrow-scroll a i {
    color: #fff;
}

.arrow-scroll a:hover {
    transition: all .3s;
    background:rgba(0,0,0,0.2);
}

/* Skills */

.container-skills-projects {
    display: flex;
    flex-wrap: wrap;
    width: 90%;
    margin: 32px auto;
    justify-content: space-between;
}

.container-skills-projects .skills-container,
.container-skills-projects .projects-container {
    width: 49%;
}

.container-skills-projects .skills-container {
    border-radius: 10px;
    background: #fff;
    background-size:cover;
    padding: 16px;
    display: flex;
    flex-wrap: wrap;
    box-shadow: 0px 0px 9px 4px rgba(0,0,0,0.32);
}

.skills-title {
    margin-top: 16px;
    width: 100%;
    padding: 20px 25px 0;
    font-size: 30px;
}

.mini-description {
    padding: 0 30px;
    margin-bottom: 16px;
    width: 100%;
}

.skills-container .each-skill {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: 16px 0;
    height: fit-content;
}

.skills-container .each-skill .skill-name {
    display: flex;
    align-items: center;
    width: 35%;
}

.skill-name i {
    margin-right: 8px;
}

.skills-info {
    width: 100%;
    padding: 10px 30px;
}

.skills-content {
    width: 100%;
    display: flex;
    justify-content: space-between  ;
    flex-wrap: wrap;
}

.progressbar {
    width: 60%;
}

.skills-container .progressbar {
    background: #d0d0d0;
/*     margin: 16px 0px; */
    display: block;
    border-radius:32px 32px 32px 32px;
}

.html, .css,
.js, .py {
    display: flex;
    align-items: center;
    color: #fff;
    background: rgb(24,129,31);
background: linear-gradient(90deg, rgba(24,129,31,1) 0%, rgba(54,154,25,1) 35%, rgba(14,215,41,1) 100%);
    padding: 8px;
    height: 27px;
    border-radius:32px 32px 32px 32px;
}

.html {
    width: 80%;
}
.css {
    width: 75%;
}
.js {
    width: 80%;
}
.py {
    width: 85%;
}

/* Projects */

.projects-container {
    width: 45%;
    background:url("./assets/main-bg.svg");
    padding: 30px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    height: fit-content;
    border-radius: 8px;
    box-shadow: 0px 0px 9px 4px rgba(0,0,0,0.32);
}

.projects-container h2, .projects-container p {
    color: #fff;
}

.project-title {
    margin: 16px 0;
    width: 100%;
    font-size: 30px;
}

.project-information {
    margin-top: 16px;
    width: 100%;
}

.project-image {
    border-radius: 5px;
    margin-top: 16px;
    width: 100%;
    overflow: hidden;
}

.project-image {
    min-width: 200px;
    max-width: 100%;
    max-height: 1000px;
    border: 1px solid #d0d0d0;
}

.project-image img {
    display: block;
    object-fit: cover;
    width: 200px;
}

.contact-container {
    width: 90%;
    margin: 0 auto;
    padding: 30px;  
    background: rgb(24,35,129);
    background: linear-gradient(90deg, rgba(24,35,129,1) 0%, rgba(25,25,154,1) 35%, rgba(0,86,255,1) 100%);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    box-shadow: 0px 0px 9px 4px rgba(0,0,0,0.32);
}

.contact-container .title-contact h2 {
    font-size: 45px;
}

.title-contact {
    width: 100%;
    margin-bottom: 16px;
}

.social-media-contact {
    width: 100%;
}

.social-media-contact ul {
    width: 100%;
}

.social-media-contact li {
    margin: 8px 0 0 0;
    list-style-type: none;
}

.social-media-contact li i {
    margin-right: 8px;
}

.social-media-contact li a {
    text-decoration: none;
}

.social-media-contact li a:hover {
    text-decoration: underline;
}

.contact-container h2,
.contact-container ul, 
.contact-container li,
.contact-container a,
.contact-container i {
    color: #fff;
}

/* Footer */
.footer-container {
    margin-top: 32px;
    width: 100%;
    text-align: center;
}

.footer-container .social-media-icons {
    width: 100%;
    background: #333;
    padding: 25px 0;
}

.social-media-icons i {
    margin: 0 8px;
    color: #fff;
    transition: all .3s;
}

.social-media-icons i:hover {
    color: rgb(51, 173, 182);
    transition: all .3s;
}

.developed-with {
    border-top: 1px solid #444;
    width: 100%;
    background: #333;
}

.developed-with .centered-box {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px 0;
}

.developed-with p {
    color: #fff;
}

.developed-with span, .developed-with i {
    color: rgb(51, 173, 182);
}

.developed-with span {
    font-family: "Droid Sans";
    font-weight: bold;
}

.developed-with i {
    margin-left: 8px;
}

.about-this {
    padding: 35px 0;
    width: 100%;
    background: #333;
}

.about-this p, .about-this b {
    font-family: "Droid Sans";
    color: #fff;
}

span.image-gallery-description {
    border-radius: 0 8px 8px 0;
}
@media only screen and (max-width:450px) {
    .phrase-container {
        font-size: 14px;
    }
    .name-container h1 {
        font-size: 45px;
    }
}

@media only screen and (max-width:500px) {
    .skill-name {
        width: 90%;
    }
    .progressbar {
        width: 90%;
    }
    .main-container .presentation-container .info-container {
        top: 40%;
        translate: (0,40%);
    }
}

@media only screen and (max-width:900px) {
    h1 {
        font-size: 50px;
    }
    .skills-title {
        margin-bottom: 16px;
    }
    .container-skills-projects {
        flex-wrap: wrap;
        justify-content: unset;
        margin: 0;
        width: 100%;
    }

    .projects-container {
        flex-wrap: wrap;
    }

    .project-information, .project-image {
        margin-top: 16px;
        width: 100%;
    }

    .project-image {
        display: flex;
        justify-content: center;
    }

    .container-skills-projects .skills-container,
    .container-skills-projects .projects-container,
    .contact-container {
        margin: 16px auto;
        width: 90%;
    }

    /* Icon show */
    .icon-container {
        display: block;
        background: transparent;
    }

    .icon-container i {
        display: block;
        padding: 16px;
        color: #fff;
    }
    .menu-container {
        display: none;
    }

    .menu-container ul {
        display: flex;
        flex-wrap: wrap;
    }

    .menu-container ul li {
        width: 100%;
        margin: 8px;
    }
    .info-container {
        margin-top: 32px;
    }
    .container-skills-projects .skills-container, 
    .container-skills-projects .projects-container {
        border-radius: 16px;
    }
    .skills-container {
        margin-bottom: 0;
    }
    .skills-container,
    .container-skills-projects .projects-container,
    .contact-container, 
    .footer-container{
        margin-top: 0;
    }
}

@media only screen and (max-width:1100px) {
    .project-image img {
        width: 100%;
    }
}